import React from 'react';
import Book from '../book/book';

import book1 from './../../images/7Habits.jpg';
import book2 from './../../images/alchamist.jpg';
import book3 from './../../images/artOfThinking.jpg';
import book4 from './../../images/subcon.jpg';

export class BookRow extends React.Component {
    render() {
        return (
            <div>
                <div class="container">
                    <div class="row">
                        <div class="col py-4">
                            <Book image={book1} />
                        </div>
                        <div class="col py-4">
                            <Book image={book2}/>
                        </div>
                        <div class="col py-4">
                            <Book image={book3}/>
                        </div>
                        <div class="col py-4">
                            <Book image={book4}/>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}